.pagination {
  display: flex;
  justify-content: center;
  padding: 1em;
}

.pagination__button {
  width: 40px;
  height: 40px;
  margin: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: 2px transparent solid;
  border-radius: 5px;
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.185);
  background: linear-gradient(60deg, rgba(111,199,234,1) 0%,
  rgba(55,184,203,1) 100%);
}

.pagination__button:hover {
  cursor: pointer;
  border-color: rgb(17, 119, 160);
}

.pagination__button:active {
  transform: scale(0.9);
}

.pagination__button--active {
  color: white;
  background: linear-gradient(60deg, rgb(42, 186, 243) 0%,
  rgb(15, 77, 87) 100%);
}