.card-body {
  background: linear-gradient(30deg, rgb(218, 243, 234) 50%,
  rgb(203, 236, 230) 50%);
}

.card-title {
  font-size: 1.5em;
}

.card-rate {
  font-size: 1.2em;
  color: rgb(189, 90, 110);
}

.card {
  height: 500px;
  overflow: hidden;
}

.card:hover {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.card-overview,
.card-buttons {
  line-height: 1.12;
  opacity: 0;
  transition: all 0.6s ease;
}

.card:hover .card-overview {
  opacity: 1;
}

.card:hover .card-buttons {
  opacity: 1;
}

.card-img-top {
  flex-basis: 50%;
  overflow: hidden;
  transition: all 0.6s ease;
}


.card:hover .card-img-top {
  flex-basis: 0%;
}

.btn {
  min-width: 150px;
}
.nav-link {
  background: linear-gradient(60deg, rgba(111,199,234,1) 0%,
  rgba(55,184,203,1) 100%);
  margin-right: 15px;
  border: 3px solid transparent;
  cursor: pointer;
}

.nav-link:hover {
  background: linear-gradient(60deg, rgb(42, 186, 243) 0%,
  rgb(15, 77, 87) 100%);
}

.nav-link:active {
  transform: scale(0.9);
}

.nav-link.active {
  font-weight: bold;
}
.favorite {
  height: 100%;
  background-color: rgb(192, 240, 229);
  margin-top: 160px;
}

.favorite li {
  margin: 5px;
  background-color: rgb(223, 243, 238);
}
.pagination {
  display: flex;
  justify-content: center;
  padding: 1em;
}

.pagination__button {
  width: 40px;
  height: 40px;
  margin: 2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  border: 2px transparent solid;
  border-radius: 5px;
  box-shadow: 1px 1px 2px 0px rgba(0, 0, 0, 0.185);
  background: linear-gradient(60deg, rgba(111,199,234,1) 0%,
  rgba(55,184,203,1) 100%);
}

.pagination__button:hover {
  cursor: pointer;
  border-color: rgb(17, 119, 160);
}

.pagination__button:active {
  transform: scale(0.9);
}

.pagination__button--active {
  color: white;
  background: linear-gradient(60deg, rgb(42, 186, 243) 0%,
  rgb(15, 77, 87) 100%);
}
body {
  background-image: url(https://lionsdigest1.com/wp-content/uploads/2020/02/2019movies-900x789.jpg);
}

h3 {
  color: #069eb6;
  font-weight: bold;
}

.container {
  font-family: "Lato", sans-serif;
  overflow: hidden;
  background: linear-gradient(60deg, rgba(133, 231, 199) 0%,
      rgba(124, 187, 173) 80%);
}

.loader {
  margin: auto;
}

.button__top {
  font-size: 18px;
  outline: none;
  padding: 12px;
  opacity: 0.6;
  cursor: pointer;
  color: #0075ff;
  background: #eee;
  border: 2px solid #0075ff;
  border-radius: 5px;
  position: fixed;
  bottom: 50px;
  right: 21%;
  z-index: 3;
  transition: all 0.5s ease;
}

.button__top:hover {
  color: #eee;
  background: #0075ff;
  opacity: 1;
}

.button__top:active {
  transform: scale(0.9);
}
